.header {
    position: relative;
    z-index: 1;
    background: #FFFFFF;
    box-shadow: 0px 1px 20px rgba(72, 3, 38, 0.07);
    padding: 28px 0;
}

.wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 30px;
}

.buttons {
    display: flex;
    gap: 30px;
}

.button {
    width: 300px;
    height: 65px;
    border-radius: 35px;
    border: none;
    font-weight: 700;
    font-size: 18px;
    line-height: 25px;
    background: #00C78B;
    color: #FFFFFF;
}

@media (max-width: 1024px) {
    .wrapper {
        flex-direction: column;
    }

    .buttons {
        gap: 20px;    
    }

    .button {
        width: 220px;
        height: 45px;
        font-size: 14px;
        line-height: 20px;
    }
}

@media (max-width: 560px) {
    .wrapper {
        gap: 20px;
    }

    .buttons {
        flex-direction: column;    
    }

}